import {checkPhone,checkPwd} from "@/common/common.js"
export default {
	data() {
		return {
			passwordType: 'password',
			isHidePassword:true,
			phone:"",
			password:""
		};
	},
	onLoad() {
		var self = this;
	},
	methods: {
		isHidePasswordClick(){
			this.isHidePassword=!this.isHidePassword
		},
		//登录
		submit() {
			if (this.phone.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入手机号'
				});
			    return;
			}
			if (this.pwd.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入密码'
				});
				return;
			}
			uni.request({
				url: '',
				data: {
					phone: this.phone,
					pwd: this.pwd
				},
				success: (res) => {
					let list=JSON.stringify(res.data);
					console.log("返回数据状态:" + list);
					if(list=="[]"){
						uni.showToast({
						    icon: 'none',
						    title: '用户名或密码错误'
						});
					    return;
					}
					//缓存手机号和密码
					if(res.data == 1) {
						uni.setStorageSync('HCuname', self.phone);
						uni.setStorageSync('HCpwd', self.pwd);
						uni.setStorageSync('islogin', true);//登录状态
						//跳转首页
						uni.navigateTo({
						    url: '/pages/index/index'
						});
					}
					uni.showToast({
						icon: 'none',
						title: '登录成功'
					})
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					})
				}
			})
		}
	}
}
